@import "../../../styles/mixins.scss";

.headerWrapper {
  position: fixed;
  width: 100%;
  z-index: 9999;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0rem 2rem;
  background-color: black;
  color: white;
  height: 5.5rem;
}

.iconsContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.iconContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  margin-right: 1rem;
  outline: none;
  border: none;
  appearance: none;
  background: transparent;
  cursor: pointer;
}

.input {
  opacity: 0;
  position: absolute;
}

.selectedIconContainer {
  @extend .iconContainer;
  animation: changeBackgroundColor infinite;
  animation-duration: 1s;
  animation-direction: alternate;
}

.iconText {
  font-size: 12px;
  user-select: none;
}

.logoContainer {
  position: relative;
  display: flex;
  align-items: center;
}

.kinoIcon {
  position: absolute;
  left: 69px;
  top: 7px;
}

.kino {
  color: white;
  font-size: 40px;
  font-weight: 900;
  text-shadow: 0 0 5px white;
  letter-spacing: 5px;
}

.show {
  font-weight: lighter;
  color: #00b8ff;
  position: absolute;
  top: 32px;
  left: 75px;
  transform: rotate(345deg);
  font-family: sans-serif;
}

.searchWrapper {
  flex-basis: 200px;
  color: rgba(255, 255, 255, 0.5);
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.searchShow {
  width: 150px;
  transition: width 1s;
  overflow: hidden;
  border-radius: 5px;
}

.searchHidden {
  width: 0px;
  overflow: hidden;
  transition: width 2s;
  transition-delay: 1s;
}

.shadow {
  height: 2.5rem;
  background: linear-gradient(180deg, black 0%, transparent 70%);
}

@include media(xs) {
  .containerBig {
    margin-top: 0.5rem;
    flex-direction: column;
  }
  .iconContainer {
    margin-top: 1rem;
  }
}

@keyframes changeBackgroundColor {
  0% {
    background: radial-gradient(
      circle,
      rgba(255, 255, 255, 0.6) 0%,
      rgba(163, 213, 241, 0.6) 20%,
      rgba(81, 175, 228, 0) 100%
    );
  }
  25% {
    background: radial-gradient(
      circle,
      rgba(255, 255, 255, 0.6) 10%,
      rgba(163, 213, 241, 0.6) 20%,
      rgba(81, 175, 228, 0) 100%
    );
  }
  50% {
    background: radial-gradient(
      circle,
      rgba(255, 255, 255, 0.6) 0%,
      rgba(163, 213, 241, 0.6) 10%,
      rgba(81, 175, 228, 0) 100%
    );
  }
  75% {
    background: radial-gradient(
      circle,
      rgba(255, 255, 255, 0.6) 10%,
      rgba(163, 213, 241, 0.6) 20%,
      rgba(81, 175, 228, 0) 100%
    );
  }
  100% {
    background: radial-gradient(
      circle,
      rgba(255, 255, 255, 0.6) 0%,
      rgba(163, 213, 241, 0.6) 0%,
      rgba(81, 175, 228, 0) 100%
    );
  }
}
